import React from "react";
import Popover from "@mui/material/Popover";
import { Link } from "react-router-dom";
import classnames from "classnames";

import "./itemAboutUs.scss";

interface IItemAboutUs {
	enableCorrection: () => void;
	disableCorrection: () => void;
}

const ItemAboutUs: React.FC<IItemAboutUs> = ({ enableCorrection, disableCorrection }) => {
	const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null);

	const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
		setAnchorEl(event.currentTarget);
		enableCorrection();
	};

	const handleClose = () => {
		setAnchorEl(null);
		disableCorrection();
	};

	const open = Boolean(anchorEl);
	const id = open ? "item-about__popover" : undefined;

	const buttonClassnames = classnames("item-about__button", { active: open });

	return (
		<div className="item-about">
			<button className={buttonClassnames} onClick={handleClick}>
				Про нас
			</button>

			<Popover
				className="item-about__popover"
				id={id}
				open={open}
				anchorEl={anchorEl}
				onClose={handleClose}
				anchorOrigin={{
					vertical: "bottom",
					horizontal: "center",
				}}
				transformOrigin={{
					vertical: "top",
					horizontal: "center",
				}}
			>
				<ul className="item-about__list">
					<li className="item-about__item">
						<Link className="item-about__link" to="/anypage" onClick={handleClose}>
							Хто ми
						</Link>
					</li>
					<li className="item-about__item">
						<Link className="item-about__link" to="/anypage" onClick={handleClose}>
							Відгуки
						</Link>
					</li>
					<li className="item-about__item">
						<Link className="item-about__link" to="/anypage" onClick={handleClose}>
							FAQ
						</Link>
					</li>
					<li className="item-about__item">
						<Link className="item-about__link" to="/anypage" onClick={handleClose}>
							Блог
						</Link>
					</li>
				</ul>
			</Popover>
		</div>
	);
};

export default ItemAboutUs;
