import React from "react";

import "./socialNetworks.scss";
import icon1 from "../../assets/images/social-networks/instagram.svg";
import icon2 from "../../assets/images/social-networks/tiktok.svg";
import icon3 from "../../assets/images/social-networks/tg.svg";
import icon4 from "../../assets/images/social-networks/viber.svg";

const SocialNetworks: React.FC = () => {
	return (
		<ul className="social-networks">
			<li className="social-networks__item">
				<a href="#" className="social-networks__link">
					<img src={icon1} alt="instagram logo" />
				</a>
			</li>
			<li className="social-networks__item">
				<a href="#" className="social-networks__link">
					<img src={icon2} alt="tiktok logo" />
				</a>
			</li>
			<li className="social-networks__item">
				<a href="#" className="social-networks__link">
					<img src={icon3} alt="telegram logo" />
				</a>
			</li>
			<li className="social-networks__item">
				<a href="#" className="social-networks__link">
					<img src={icon4} alt="viber logo" />
				</a>
			</li>
		</ul>
	);
};

export default SocialNetworks;
