import React from "react";
import { Link } from "react-router-dom";

import SocialNetworks from "../socialNetworks/SocialNetworks";
import { scrollToTop } from "../../services";

import "./footer.scss";

const Footer: React.FC = () => {
	return (
		<footer className="footer">
			<div className="container">
				<nav className="footer__inner">
					<div className="footer__copyright">
						<p className="footer__copyright-text">© 2022. Просто Паста.</p>
						<p className="footer__copyright-text">Всі права застережено.</p>
						<Link className="footer__policy" to="/anypage">
							Політика конфіденційності
						</Link>
					</div>
					<div className="footer__content">
						<p className="footer__ukraine">Зроблено в Україні</p>
						<ul className="footer__menu-list">
							<li className="footer__menu-item">
								<Link className="footer__menu-link" to="/" onClick={scrollToTop}>
									На головну
								</Link>
							</li>
							<li className="footer__menu-item">
								<Link className="footer__menu-link" to="/catalogue" onClick={scrollToTop}>
									Продукція
								</Link>
							</li>
							<li className="footer__menu-item">
								<Link className="footer__menu-link" to="/anypage">
									FAQ
								</Link>
							</li>
							<li className="footer__menu-item">
								<Link className="footer__menu-link" to="/anypage">
									Про нас
								</Link>
							</li>
							<li className="footer__menu-item">
								<Link className="footer__menu-link" to="/anypage">
									Контакти
								</Link>
							</li>
						</ul>
					</div>
					<div className="footer__social">
						<p className="footer__social-label">Ми в соцмережах :</p>
						<div className="footer__social-item">
							<SocialNetworks />
						</div>
					</div>
				</nav>
			</div>
		</footer>
	);
};

export default Footer;
