import React from "react";

const useScrollLock = () => {
	const [isScrollLocked, setScrollLocked] = React.useState<boolean>(false);
	const [scrollbarWidth, setScrollbarWidth] = React.useState<number>(0);
	const bodyRef = React.useRef<HTMLElement>(document?.body);

	React.useEffect(() => {
		bodyRef.current = document.body;
		getScrollbarWidth();
	}, []);

	const toggleScrollLock = () => {
		setScrollLocked((prevState) => {
			if (prevState) {
				bodyRef.current.style.overflowY = "scroll";
				bodyRef.current.style.paddingRight = "0px";
			} else {
				bodyRef.current.style.overflowY = "hidden";
				bodyRef.current.style.paddingRight = scrollbarWidth + "px";
			}

			return !prevState;
		});
	};

	const lockScroll = () => {
		setScrollLocked(true);
		bodyRef.current.style.overflowY = "hidden";
		bodyRef.current.style.paddingRight = scrollbarWidth + "px";
	};

	const unlockScroll = () => {
		setScrollLocked(false);
		bodyRef.current.style.overflowY = "scroll";
		bodyRef.current.style.paddingRight = "0px";
	};

	const getScrollbarWidth = () => {
		const div = document.createElement("div");
		div.style.cssText = `
            width: 50px;
            height: 50px;
            overflow-y: scroll;
            opacity: 0;
            visibility: hidden;
        `;
		document.body.append(div);

		const scrollbarWidth = div.offsetWidth - div.clientWidth;

		div.remove();

		setScrollbarWidth(scrollbarWidth);
	};

	return {
		isScrollLocked,
		toggleScrollLock,
		lockScroll,
		unlockScroll,
		scrollbarWidth,
	};
};

export default useScrollLock;
