import React from "react";
import Popover from "@mui/material/Popover";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import { Link } from "react-router-dom";
import classnames from "classnames";

import { scrollToTop } from "../../services";

import "./headerMenu.scss";

interface HeaderMenuProps {
	enableCorrection: () => void;
	disableCorrection: () => void;
}

const HeaderMenu: React.FC<HeaderMenuProps> = ({ enableCorrection, disableCorrection }) => {
	const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null);

	const open = Boolean(anchorEl);
	const buttonClassnames = classnames("header-menu__button", { active: open });

	const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
		setAnchorEl(event.currentTarget);
		enableCorrection();
	};

	const handleClose = () => {
		setAnchorEl(null);
		disableCorrection();
	};

	const handleCloseAndScroll = () => {
		handleClose();
		scrollToTop();
	};

	return (
		<div className="header-menu">
			<button
				className={buttonClassnames}
				onClick={handleClick}
				aria-label="open or close menu"
				aria-expanded={open}
				aria-controls="header-menu__popover"
			>
				<svg className="ham" viewBox="0 0 100 100" width="100%">
					<path
						className="line top"
						d="m 30,33 h 40 c 13.100415,0 14.380204,31.80258 6.899646,33.421777 -24.612039,5.327373 9.016154,-52.337577 -12.75751,-30.563913 l -28.284272,28.284272"
					></path>
					<path
						className="line middle"
						d="m 70,50 c 0,0 -32.213436,0 -40,0 -7.786564,0 -6.428571,-4.640244 -6.428571,-8.571429 0,-5.895471 6.073743,-11.783399 12.286435,-5.570707 6.212692,6.212692 28.284272,28.284272 28.284272,28.284272"
					></path>
					<path
						className="line bottom"
						d="m 69.575405,67.073826 h -40 c -13.100415,0 -14.380204,-31.80258 -6.899646,-33.421777 24.612039,-5.327373 -9.016154,52.337577 12.75751,30.563913 l 28.284272,-28.284272"
					></path>
				</svg>
			</button>
			<Popover
				className="header-menu__popover"
				id="header-menu__popover"
				open={open}
				anchorEl={anchorEl}
				onClose={handleClose}
				anchorOrigin={{
					vertical: "bottom",
					horizontal: "center",
				}}
				transformOrigin={{
					vertical: "top",
					horizontal: "center",
				}}
			>
				<ul className="header-menu__list">
					<li className="header-menu__item">
						<Link
							className="header-menu__link"
							to="/catalogue"
							aria-label="go to page Products"
							onClick={handleCloseAndScroll}
						>
							Продукція
						</Link>
					</li>
					<li className="header-menu__item">
						<Link
							className="header-menu__link"
							to="/anypage"
							aria-label="go to page Partnership"
							onClick={handleClose}
						>
							Співробітництво
						</Link>
					</li>
					<li className="header-menu__item">
						<Link
							className="header-menu__link"
							to="/anypage"
							aria-label="go to page Contacts"
							onClick={handleClose}
						>
							Контакти
						</Link>
					</li>
					<li className="header-menu__item">
						<Accordion className="menu-accordeon" disableGutters square>
							<AccordionSummary
								expandIcon={
									<svg
										className="menu-accordeon__icon"
										width="16"
										height="16"
										viewBox="0 0 16 16"
										fill="none"
										xmlns="http://www.w3.org/2000/svg"
									>
										<path
											d="M7.99952 9.44844L11.2995 6.14844L12.2422 7.0911L7.99952 11.3338L3.75685 7.0911L4.69952 6.14844L7.99952 9.44844Z"
											fill="#282828"
										/>
									</svg>
								}
								aria-controls="panel1a-content"
								id="panel1a-header"
								className="menu-accordeon__summary"
							>
								Про нас
							</AccordionSummary>
							<AccordionDetails className="menu-accordeon__details">
								<ul className="menu-accordeon__list">
									<li className="menu-accordeon__item">
										<Link
											className="menu-accordeon__link"
											to="/anypage"
											onClick={handleClose}
											aria-label="go to page Хто ми"
										>
											Хто ми
										</Link>
									</li>
									<li className="menu-accordeon__item">
										<Link
											className="menu-accordeon__link"
											to="/anypage"
											onClick={handleClose}
											aria-label="go to page Відгуки"
										>
											Відгуки
										</Link>
									</li>
									<li className="menu-accordeon__item">
										<Link
											className="menu-accordeon__link"
											to="/anypage"
											onClick={handleClose}
											aria-label="go to page FAQ"
										>
											FAQ
										</Link>
									</li>
									<li className="menu-accordeon__item">
										<Link
											className="menu-accordeon__link"
											to="/anypage"
											onClick={handleClose}
											aria-label="go to page Блог"
										>
											Блог
										</Link>
									</li>
								</ul>
							</AccordionDetails>
						</Accordion>
					</li>
				</ul>
			</Popover>
		</div>
	);
};

export default HeaderMenu;
