import React from "react";
import { HashRouter, Route, Routes } from "react-router-dom";
import { useInView } from "react-intersection-observer";
import classnames from "classnames";

import {
	viewportWidthContext,
	fixedPositionCorrectionContext,
	scrollLockContext,
	cartVisibilityContext,
} from "../context";
import { useMatchMedia, useScrollLock } from "../hooks";
import Header from "./header/Header";
import Footer from "./footer/Footer";
import Loading from "./loading/Loading";
import ScrollToTop from "./scrollToTop";
import CartDrawer from "./cartDrawer/CartDrawer";

const HomePage = React.lazy(() => import(/* webpackChunkName: "HomePage" */ "../pages/HomePage"));
const ProductPage = React.lazy(() => import(/* webpackChunkName: "ProductPage" */ "../pages/ProductPage/ProductPage"));
const AnyPage = React.lazy(() => import(/* webpackChunkName: "AnyPage" */ "../pages/AnyPage/AnyPage"));
const CataloguePage = React.lazy(
	() => import(/* webpackChunkName: "CataloguePage" */ "../pages/CataloguePage/CataloguePage")
);

function App() {
	const { ref, inView } = useInView({
		threshold: 1,
		initialInView: true,
	});
	const { isMobile, isTablet, isDesktop } = useMatchMedia();
	const { isScrollLocked, toggleScrollLock, lockScroll, unlockScroll, scrollbarWidth } = useScrollLock();

	// values for fixedPositionCorrectionContext (aim - counting padding right for elements with the next positions: fixed & absolute)
	const [correction, setCorrection] = React.useState<boolean>(false);
	const enableCorrection = () => {
		setCorrection(true);
	};
	const disableCorrection = () => {
		setCorrection(false);
	};

	const [isCartOpened, setCartOpened] = React.useState<boolean>(false);

	const mainTagClassNames = classnames("main", { blured: isCartOpened });

	const showCart = () => {
		setCartOpened(true);
		setCorrection(true);
		lockScroll();
	};

	const hideCart = () => {
		setCartOpened(false);
		setCorrection(false);
		unlockScroll();
	};

	return (
		<HashRouter>
			<viewportWidthContext.Provider value={{ isMobile, isTablet, isDesktop }}>
				<fixedPositionCorrectionContext.Provider value={{ correction, enableCorrection, disableCorrection }}>
					<scrollLockContext.Provider
						value={{ isScrollLocked, toggleScrollLock, lockScroll, unlockScroll, scrollbarWidth }}
					>
						<cartVisibilityContext.Provider value={{ isCartOpened, showCart, hideCart }}>
							<div className="wrapper">
								<ScrollToTop />
								<Header isLarge={inView} isDesktop={isDesktop} />
								<main className={mainTagClassNames} data-offset={inView}>
									{/* trigger element for the intersection observer instance (connected with the Header) */}
									<div className="main__trigger" ref={ref} aria-hidden="true"></div>
									<React.Suspense fallback={<Loading />}>
										<Routes>
											<Route path="/" element={<HomePage />} />
											<Route path="/products/:id" element={<ProductPage />} />
											<Route path="/catalogue" element={<CataloguePage />} />
											<Route path="/anypage" element={<AnyPage />} />
											<Route path="*" element={<AnyPage />} />
										</Routes>
									</React.Suspense>
								</main>
								<Footer />
								<CartDrawer isOpened={isCartOpened} onClose={hideCart} />
							</div>
						</cartVisibilityContext.Provider>
					</scrollLockContext.Provider>
				</fixedPositionCorrectionContext.Provider>
			</viewportWidthContext.Provider>
		</HashRouter>
	);
}

export default App;
