import React from "react";

interface fixedPositionCorrectionContextValue {
	correction: boolean;
	enableCorrection: () => void;
	disableCorrection: () => void;
}

export const fixedPositionCorrectionContext = React.createContext<fixedPositionCorrectionContextValue>({
	correction: false,
	enableCorrection: () => {},
	disableCorrection: () => {},
});
