import React from "react";
import Spinner from "../spinner/Spinner";

import "./loading.scss";

const Loading: React.FC = () => {
	return (
		<div className="item-loading" aria-hidden="true">
			<div className="container">
				<div className="item-loading__inner">
					<Spinner />
					<p className="item-loading__message">
						<span className="item-loading__message-letter">Х</span>
						<span className="item-loading__message-letter">в</span>
						<span className="item-loading__message-letter">и</span>
						<span className="item-loading__message-letter">л</span>
						<span className="item-loading__message-letter">и</span>
						<span className="item-loading__message-letter">н</span>
						<span className="item-loading__message-letter">о</span>
						<span className="item-loading__message-letter">ч</span>
						<span className="item-loading__message-letter">к</span>
						<span className="item-loading__message-letter">у</span>
						<span className="item-loading__message-letter">.</span>
						<span className="item-loading__message-letter">.</span>
						<span className="item-loading__message-letter">.</span>
					</p>
				</div>
			</div>
		</div>
	);
};

export default Loading;
